<template>
  <div class="login">
    <!-- <v-alert
        success
        :value="alert"
        transition="scale-transition"
      >
       登陆成功
      </v-alert> -->
    <div class="login-input-ak">
      <v-text-field label="AccessToken,也可以扫描二维码↗" v-model="ak" :counter="36" required></v-text-field>
      <v-btn block secondary dark @click.native="login">登录</v-btn>
    </div>
  </div>
</template>

<script>
import {
  mapActions,
  mapMutations
} from 'vuex';
import EventBus from '@/event-bus';
export default {
  name: 'login',
  comments: {},
  data() {
    return {
      ak: '',
      alert: false
    }
  },

  mounted() {
    if (this.$route.path == '/login') {
      Qrcode.init($('[node-type=qr-btn]'))
    }
    $('.tmp').bind('click', function() {
      EventBus.$emit('input-ak-through-qrcode', $(this).val())
    })
  },
  methods: {
    ...mapActions('appShell/appHeader', [
      'setAppHeader'
    ]),
    ...mapMutations('userInfo/', [
      'login'
    ]),
    login() {
      if (this.ak.length !== 36) {
        alert('请输入正确的accesstoken！')
        return
      }
      this.$http.post('https://cnodejs.org/api/v1/accesstoken', {
        accesstoken: this.ak
      }).then(res => {
        this.alert = !this.alert
        this.$store.commit('login');
        localStorage.UserInfo =
          JSON.stringify({
            id: res.data.id,
            AccessToken: this.ak,
            UserName: res.data.loginname,
            AvatarURL: res.data.avatar_url
          })
        this.$router.go(-1)
        this.ak = ''
      }).catch(e => {
        alert(e)
         this.$router.go(-1)
      })
    }
  },
  activated() {
    EventBus.$on('input-ak-through-qrcode', eventData => {
      this.ak = eventData
    }),
      this.setAppHeader({
        show: true,
        title: '请登录',
        showMenu: false,
        showBack: true,
        showLogo: false,
        actions: [{
          icon: 'aspect_ratio',
          act: function(eventData) {
            $('.qr-btn').click()
          }
        }]
      });
  }
}
</script>

<style lang="stylus" acoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: #000
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom

.login
    display: flex; 
    justify-content: center; 
    align-items: center;
.login-input-ak
    width 80%

</style>



